import Splide from '@splidejs/splide'
import { playPageIntro } from '../components/page-intro'
import { breakpointDown } from '../constants'
import { isSP } from '../utils'

function handleConceptProductSplide() {
  const conceptSplides = document.querySelectorAll('.js-concept-product-splide')

  conceptSplides.forEach((productSplide) => {
    new Splide(productSplide, {
      type: 'fade',
      rewind: true,
      arrows: false,
      pagination: false,
      autoplay: true,
      pauseOnHover: false,
      speed: 2000,
      interval: 2000,
    }).mount()
  })
}

function setBodyClassName() {
  document.body.classList.toggle('is-body-scroll-active', window.scrollY > 0)
}

function MobileEvents() {
  const visualSection = document.querySelector('#visual-section')
  const videoWrapper = document.querySelector('#main-video-wrapper')
  const initVideoPositionTop = videoWrapper.offsetTop
  const initVideoPositionBottom =
    videoWrapper.clientHeight + videoWrapper.offsetTop

  function setVideoPosition() {
    if (!isSP()) {
      videoWrapper.style.removeProperty('top')
      videoWrapper.style.removeProperty('bottom')

      return
    }

    if (window.scrollY > 0) {
      const positionTop = Math.max(0, initVideoPositionTop - window.scrollY)
      const positionBottom = Math.max(
        window.innerHeight - window.scrollY - initVideoPositionBottom,
        0
      )

      videoWrapper.style.top = positionTop + 'px'
      videoWrapper.style.bottom = positionBottom + 'px'
    } else {
      videoWrapper.style.removeProperty('top')
      videoWrapper.style.removeProperty('bottom')
    }
  }

  function handleVisualSectionScrollTranslate() {
    if (!isSP()) {
      visualSection.style.removeProperty('opacity')

      return
    }

    const height = visualSection.clientHeight
    const rect = visualSection.getBoundingClientRect()
    const heightInScreen = height - Math.abs(rect.top)
    if (heightInScreen <= 0) {
      return
    }
    if (heightInScreen === height) {
      visualSection.style.removeProperty('opacity')

      return
    }
    const opacity = Math.max(heightInScreen / height - 0.3, 0)
    visualSection.style.opacity = opacity.toFixed(2)
  }

  setVideoPosition()
  handleVisualSectionScrollTranslate()

  window.addEventListener('scroll', () => {
    setVideoPosition()
    handleVisualSectionScrollTranslate()
  })
}

export function TopPageEvent() {
  let mobileEventAdded = false
  const video = document.querySelector('#main-video')
  const conceptImage1 = document.querySelector('.js-concept-translate-img-1')
  const conceptImage2 = document.querySelector('.js-concept-translate-img-2')
  const productSlider1 = document.querySelector('.js-product-slider-1')
  const productSlider2 = document.querySelector('.js-product-slider-2')

  function handleProductSlideTranslate() {
    const parent = productSlider1.parentElement

    const elementPos =
      parent.getBoundingClientRect().top + parent.clientHeight / 2
    const windowCenterPos = window.innerHeight * 0.5
    const distanceY = (windowCenterPos - elementPos) * 0.2

    productSlider1.style.transform = `translateY(-${distanceY}px)`
    productSlider2.style.transform = `translateY(${distanceY}px)`
  }

  function handleConceptBoxImageTranslate() {
    const parent = conceptImage1.parentElement
    const elementPos = parent.getBoundingClientRect().top

    const windowCenterPos = window.innerHeight * 0.8
    const distanceX = Math.min((windowCenterPos - elementPos) * 0.1, 40)
    const distanceY = Math.min((windowCenterPos - elementPos) * 0.33, 132)

    conceptImage1.style.transform = `translateX(${distanceX}px)`
    conceptImage2.style.transform = `translateY(-${distanceY}px)`
  }

  // INIT PAGE EVENT
  playPageIntro()

  setTimeout(() => {
    video.play()
    video.setAttribute('autoplay', true)
  }, 1000)

  setBodyClassName()
  handleConceptProductSplide()
  handleConceptBoxImageTranslate()
  handleProductSlideTranslate()

  // PAGE SCROLL EVENT
  window.addEventListener('scroll', () => {
    setBodyClassName()
    handleProductSlideTranslate()
    handleConceptBoxImageTranslate()
  })

  // WINDOW RESIZE EVENT
  window.addEventListener('resize', () => {
    handleConceptBoxImageTranslate()
    handleProductSlideTranslate()
  })

  // MOBILE EVENTS
  const mobileMediaMatch = matchMedia(breakpointDown.md)

  function addMobileEvents() {
    if (mobileEventAdded === true) {
      return
    }

    MobileEvents()
    mobileEventAdded = true
  }

  if (mobileMediaMatch.matches) {
    addMobileEvents()
  }

  mobileMediaMatch.addEventListener('change', (event) => {
    if (event.matches) {
      addMobileEvents()
    }
  })
}
